<template>
  <div class="container">
    <Header title="" type="home"></Header>
    <van-swipe class="index-swipe" :autoplay="3000" :height="224" indicator-color="white">
      <van-swipe-item v-for="(item,index) in dataImage[local].banner" :key="index"><img style="display:block;" :src="item.image" alt="" /></van-swipe-item>
    </van-swipe>
    <div class="content-wrap">
      <div class="border-out card-floor-border">
        <div class="border-in">
          <div class="card-floor">
            <div class="title flex-middle">
              <div class="left flex-item text-ellipsis">
                {{t('index.無效協議')}}
              </div>
              <div class="right" @click="$router.push({name:'Lp'})">{{t('index.我的LP池')}} <img class="arrow" src="@/assets/images/arrow-right@2x.png" alt=""></div>
            </div>
            <div class="overview">
              <div class="item border-1px-t">
                <div class="key">{{t('index.可提現')}}(USDT)</div>
                <div class="value flex-middle">
                  <span class="text-ellipsis">
                    {{homeData.jt_withdraw}}
                  </span>
                </div>
                <div class="redius-btn flex-shrik" @click="withdraw(1)">{{t('index.提取')}}</div>
              </div>
              <div class="item border-1px-t">
                <div class="key">{{t('index.總入金')}}(USDT)</div>
                <div class="value flex-middle">
                  <span class="text-ellipsis" style="color:#fff">
                    {{homeData.jt_totalinvest}}
                  </span>
                </div>
              </div>
            </div>
            <div class="overview-in border-1px-t">
              <div class="item">
                <div class="key">{{t('index.總收益')}}(USDT)</div>
                <div class="value">
                  <span class="text-ellipsis">
                    {{homeData.jt_income}}
                  </span>
                </div>
              </div>
              <div class="item">
                <div class="key">{{t('index.今日入金')}}(USDT)</div>
                <div class="value">
                  <span class="text-ellipsis">
                    {{homeData.jt_dayinvest}}
                  </span>
                </div>
              </div>
              <div class="item" style="margin-top:31px;">
                <div class="key">{{t('index.收益計算器')}}</div>
                <div class="value">
                  <img style="margin-left:11px;" src="@/assets/images/calculator.png" alt="">
                </div>
              </div>
            </div>
          </div>
          <div class="border-out">
            <div class="border-in">
              <div class="form-wrap" id="agreement">
                <div class="top">
                  <div class="agreement flex-between">
                    <div class="left">
                      <div class="key">{{t('index.質押協議')}}</div>
                      <img class="icon" src="@/assets/images/usdt@2x.png" @error="$setErrorImg" alt="">
                    </div>
                    <div class="right" @click="$router.push({name:'PledgeRecords'})">
                      {{t('index.我的質押協議列表')}}
                      <img src="@/assets/images/arrow-right@2x.png" alt="">
                    </div>
                  </div>
                  <div class="select-wrap">
                    <div class="item">
                      <div class="key">{{t('index.質押期數')}}(7D)</div>
                      <div class="select" @click="showPickerDay = true">
                        {{selectDay.text}}<img class="arrow" src="@/assets/images/arrow-down@2x.png" alt="">
                      </div>
                    </div>
                    <div class="item">
                      <div class="key">{{t('index.質押數量')}}(USDT)</div>
                      <div class="select" @click="showPickerAmount = true">
                        {{selectAmount.text}} <img class="arrow" src="@/assets/images/arrow-down@2x.png" alt="">
                      </div>
                    </div>
                    <div class="item">
                      <div class="key">{{t('index.預估年化率')}}</div>
                      <div class="value" style="font-size:37px;">{{apy}}%</div>
                    </div>
                  </div>
                </div>
                <div class="btn-wrap" style="margin-top:4px;">
                  <van-button :loading="loading" v-if="Number(allowance) >= Number(selectAmount.amount)" type="primary" @click="depositHandle" :loading-text="t('index.授權存證') + '...'">{{t("index.授權存證")}}</van-button>
                  <van-button :loading="loading" v-else type="primary" @click="approve" :loading-text="t('index.授權') + '...'">{{t("index.授權")}} USDT</van-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="border-out">
        <div class="border-in">
          <div class="account-wrap flex-between">
            <div class="left">{{t('index.錢包地址')}}</div>
            <div class="right flex-middle">{{address.substring(0, 8)}}****{{address.substring(address.length - 8, address.length)}}<img class="copy clip-button" :data-clipboard-text="address" src="@/assets/images/copy@2x.png" alt=""></div>
          </div>
        </div>
      </div>
      <div class="intro-wrap" id="share">
        <div class="wrap flex-middle">
          <div class="item">
            <div class="border-out">
              <div class="border-in">
                <div class="title">{{t('index.邀請鏈接')}}</div>
                <div class="key">FID</div>
                <img class="clip-button" :data-clipboard-text="linkFid" src="@/assets/images/copy.png" alt="">
              </div>
            </div>
          </div>
          <div class="item">
            <div class="border-out">
              <div class="border-in">
                <div class="title">{{t('index.邀請鏈接')}}</div>
                <div class="key">CID</div>
                <img class="clip-button" :data-clipboard-text="linkCid" src="@/assets/images/copy.png" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="border-out">
        <div class="border-in">
          <div class="income-wrap" id="income">
            <div class="title flex-middle flex-between">
              <div class="left flex-middle">{{t('index.總金額')}} <img src="@/assets/images/show@2x.png" v-if="viewMoney" @click="viewMoney = !viewMoney" @error="$setErrorImg" alt=""><img v-else @click="viewMoney = !viewMoney" src="@/assets/images/hide@2x.png" @error="$setErrorImg" alt=""></div>
              <div class="right flex-middle" @click="$router.push({name:'Income'})">
                {{t('index.收益明細')}} <img class="arrow" src="@/assets/images/arrow-right@2x.png" @error="$setErrorImg" alt="">
              </div>
            </div>
            <div class="amount text-ellipsis" v-if="viewMoney">
              {{homeData.dt_withdraw}}
              <div class="small">USDT</div>
            </div>
            <div class="amount text-ellipsis" v-else>*************</div>
            <div class="redius-btn flex-shrik" @click="withdraw(2)">{{t('index.提取')}}</div>
            <div class="income-overview flex">
              <div class="item">
                <div class="key">{{t('index.昨日收益')}}</div>
                <div class="value">
                  <span class="text-ellipsis">
                    {{homeData.dt_zrincome}}
                  </span>USDT
                </div>
              </div>
              <div class="item">
                <div class="key">{{t('index.動態總收益')}}</div>
                <div class="value">
                  <span class="text-ellipsis">
                    {{homeData.dt_income}}
                  </span>USDT
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="contact-wrap" id="contact">
        <div class="title">{{t('index.聯繫我們')}}</div>
        <div class="contact flex-between">
          <div class="item">
            <img class="logo" src="@/assets/images/facebook@2x.png" alt="">
            <div class="text">Facebook</div>
          </div>
          <div class="item">
            <img class="logo" src="@/assets/images/twitter@2x.png" alt="">
            <div class="text">Twitter</div>
          </div>
          <div class="item">
            <img class="logo" src="@/assets/images/email@2x.png" alt="">
            <div class="text">Email</div>
          </div>
          <div class="item">
            <img class="logo" src="@/assets/images/telegram@2x.png" alt="">
            <div class="text">Telegram</div>
          </div>
          <div class="item">
            <img class="logo" src="@/assets/images/discord@2x.png" alt="">
            <div class="text">Discord</div>
          </div>
        </div>
        <div class="copyright">
          @Copyright OCEAN {{curYear}}. All Rights Reserved
          Please don't trust fake accounts. For more info, please
          follow our official channels!
        </div>
      </div>
    </div>
    <van-popup v-model:show="showPickerDay" position="bottom" v-if="columnsDay.length > 0">
      <van-picker :columns="columnsDay" @confirm="onConfirmDay" @cancel="showPickerDay = false" />
    </van-popup>
    <van-popup v-model:show="showPickerAmount" position="bottom" v-if="amounts.length > 0">
      <van-picker :columns="amounts" @confirm="onConfirmAmount" @cancel="showPickerAmount = false" />
    </van-popup>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, onMounted, nextTick } from "vue";
import { useI18n } from "vue-i18n";
import config from "@/utils/config";
import investAbi from "@/utils/investAbi";
import tokenAbi from "@/utils/abi";
import { useStore } from "vuex";
import { getLang } from "@/utils/i18n";
import BigNumber from "bignumber.js"
import { MaxUint256 } from "@ethersproject/constants";
import Clipboard from 'clipboard'

import { Toast } from 'vant';
import { getHomeData, getNonce, getMoney } from "@/api"

export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts),
      web3: computed(() => store.state.provider),
      investContract: null,
      usdtContract: null,
      balance: 0,
      local: getLang() || "zh-TW",
      dataImage: {
        'zh-TW': { banner: [{ image: require('@/assets/images/banner_tw@2x.png') }] },
        'en': { banner: [{ image: require('@/assets/images/banner_en@2x.png') }] },
        'ja': { banner: [{ image: require('@/assets/images/banner_ja@2x.png') }] },
        'ko': { banner: [{ image: require('@/assets/images/banner_ko@2x.png') }] }
      },
      curYear: new Date().getFullYear(),
      categoryList: [
        {
          name: t('index.首頁'),
          id: 0,
          target: '#home'
        }, {
          name: t('index.質押協議'),
          id: 1,
          target: '#agreement'
        },
        {
          name: t('index.分享中心'),
          id: 2,
          target: '#share'
        },
        {
          name: t('index.餘額'),
          id: 3,
          target: '#income'
        },
        {
          name: t('index.聯繫我們'),
          id: 4,
          target: '#contact'
        }
      ],
      cid: 0,
      showPickerDay: false,
      showPickerAmount: false,
      columnsDay: [],
      amounts: [],
      allowance: '',
      selectDay: {
        id: '',
        text: ''
      },
      selectAmount: {
        id: '',
        text: ''
      },
      loading: false,
      transaction: {},
      linkFid: '',
      linkCid: '',
      homeData: {
        dt_income: "0.00",
        dt_withdraw: "0.00",
        dt_zrincome: "0.00",
        jt_dayinvest: "0.00",
        jt_income: "0.00",
        jt_totalinvest: "0.00",
        jt_withdraw: "0.00",
      },
      apy: '0.00',
      viewMoney: true,
      initContract: () => {
        data.linkFid = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}&type=1`
        data.linkCid = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}&type=2`
        data.investContract = data.web3.contract(investAbi, config.investAddress)
        data.usdtContract = data.web3.contract(tokenAbi, config.usdtAddress)
        data.initClip()
        data.initData()
      },
      initClip: () => {
        let clipboard = new Clipboard('.clip-button')
        clipboard.on('success', (e) => {
          Toast('复制成功')
          e.clearSelection()
        })
        clipboard.on('error', () => {
          Toast('复制失败')
        })
      },
      initData: () => {
        data.getAllowance()
        data.getAllStakeType()
        data.getHomeData()
        data.getAllInvestType()
      },
      getHomeData: () => {
        getHomeData({ address: data.address }).then((res) => {
          data.homeData.dt_income = Number(res.data.dt_income).toFixed(2) + ''
          data.homeData.dt_withdraw = Number(res.data.dt_withdraw).toFixed(2) + ''
          data.homeData.dt_zrincome = Number(res.data.dt_zrincome).toFixed(2) + ''
          data.homeData.jt_dayinvest = Number(res.data.jt_dayinvest).toFixed(2) + ''
          data.homeData.jt_income = Number(res.data.jt_income).toFixed(2) + ''
          data.homeData.jt_totalinvest = Number(res.data.jt_totalinvest).toFixed(2) + ''
          data.homeData.jt_withdraw = Number(res.data.jt_withdraw).toFixed(2) + ''
        })
      },
      handleTransaction: (id) => {
        window.clearInterval(data.transaction[id]);
        data.transaction[id] = setInterval(() => {
          data.web3.trx.getConfirmedTransaction(id).then(res => {
            if (res.ret[0].contractRet === "SUCCESS") {
              window.clearInterval(data.transaction[id]);
              Toast.clear();
              Toast(t('交易成功'))
              data.loading = false
              data.initData()
            } else {
              window.clearInterval(data.transaction[id]);
              data.initData()
              Toast.clear();
              data.loading = false
              Toast(t('交易失敗'))
            }
          }).catch(() => {
            //  Toast('交易失败' + JSON.stringify(err))
          });
        }, 2000);
      },
      getAllowance: async () => {
        let allowance = await data.usdtContract.methods.allowance(data.address, config.investAddress).call()
        data.allowance = allowance.toString()
      },
      approve: async () => {
        let hash = await data.usdtContract.methods.approve(config.investAddress, MaxUint256).send({ from: data.address })
        data.loading = true
        data.handleTransaction(hash)
      },
      getAllStakeType: async () => {
        // let types = await data.investContract.methods.getAllStakeIds().call()
        // console.log(types,'getAllStakeType')
        //  if(Object.prototype.toString.call(types) == '[object Object]') {
        //    types = types.ids
        //  }
        // let arr = []
        // for(let i = 0; i < types.length;i++) {
        //   let info = await data.investContract.methods.stakeTypeInfo(types[i].toString()).call()
        //   console.log(types[i].toString(), info)
        //   arr.push({
        //      id: types[i].toString(),
        //      dayAmount: info.dayAmount.toString(),
        //      text: info.dayAmount.toString() / 7
        //   })
        // }
        let arr = [
          {
           id: 1,
           dayAmount: 7,
           text: 1
          },
          {
           id: 2,
           dayAmount: 14,
           text: 2
          },
          {
           id: 3,
           dayAmount: 21,
           text: 3
          },
          {
           id: 4,
           dayAmount: 28,
           text: 4
          },
          {
           id: 5,
           dayAmount: 35,
           text: 5
          },
          {
           id: 6,
           dayAmount: 42,
           text: 6
          },
          {
           id: 7,
           dayAmount: 49,
           text: 7
          },
          {
           id: 8,
           dayAmount: 56,
           text: 8
          },
          {
           id: 9,
           dayAmount: 63,
           text: 9
          },
          {
           id: 10,
           dayAmount: 70,
           text: 10
          }
        ]
        data.columnsDay =  arr;
        data.selectDay = data.columnsDay[0]
      },
      getAllInvestType: async () => {
        let types = await data.investContract.methods.getAllInvestIds().call()
          if(Object.prototype.toString.call(types) == '[object Object]') {
           types = types.ids
         }
        // let decimals = await data.usdtContract.methods.decimals().call()
        let arr = []
        for(let i = 0; i < types.length;i++) {
          let info = await data.investContract.methods.investTypeInfo(types[i].toString()).call()
          console.log(types[i].toString(), info)
          arr.push({
            id: types[i].toString(),
            amount: info.amount.toString(),
            text: new BigNumber(info.amount.toString()).dividedBy(10 ** 6).toFixed(),
            inomeRate: info.inomeRate.toString()
          })
        }
        data.amounts = arr;
        data.selectAmount = data.amounts[0]
        data.getApy()
      },
      getApy: () => {
        let apy = Number(365 * data.selectAmount.inomeRate * data.selectAmount.text * 100 / 10000 / data.selectAmount.text).toFixed(2)
        data.apy = apy
      },
      TabInkBarHandle: () => {
        let curTabItem = document.querySelectorAll('.tab .tab-item')[data.cid]
        let oTabInkBar = document.querySelector('.tab .tab-ink-bar')
        oTabInkBar.style.left = curTabItem.offsetLeft + (curTabItem.offsetWidth / 2) - (oTabInkBar.offsetWidth / 2) + 'px'
        // let oTab = document.querySelector('.tab')
        // let scrollLeft = oTab.offsetWidth / 2 - (curTabItem.offsetLeft + curTabItem.offsetWidth / 2)
        // if ((scrollLeft) < 0) {
        //   oTab.scrollLeft = Math.abs(scrollLeft)
        // } else {
        //   oTab.scrollLeft = 0
        // }
      },
      tabChangeHandle: async (item) => {
        console.log(item, 'item')
        let target = document.querySelector(item.target)
        data.cid = item.id
        await nextTick()
        if (target) {
          target.scrollIntoView({ behavior: 'smooth', block: "center", inline: "nearest" })
        }
        // data.TabInkBarHandle()
      },
      depositHandle: async () => {
        let balane = await data.usdtContract.methods.balanceOf(data.address).call()
        if (Number(balane.toString()) < Number(data.selectAmount.amount)) {
          Toast('USDT餘額不足')
          return
        }
        console.log(data.selectDay.id, data.selectAmount.id)
        let hash = await data.investContract.methods.stake(data.selectDay.id+ '', data.selectAmount.id).send({ from: data.address })
        data.loading = true
        data.handleTransaction(hash)
      },
      withdraw: async (type) => {
        let nonceInfo = await getNonce({ address: data.address });
        let nonce = nonceInfo.nonce;
        let signature = await data.web3.trx.sign(data.web3.toHex(nonce));
        await getMoney({
          bankname: type,
          address: data.address,
          signature: signature
        })
        data.initData()
      },
      onConfirmDay: (value) => {
        data.selectDay = value
        data.showPickerDay = false
      },
      onConfirmAmount: (value) => {
        data.selectAmount = value
        data.showPickerAmount = false
        data.getApy()
      }
    })
    onMounted(() => {
      // data.TabInkBarHandle()
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.index-swipe {
  position: relative;
}
.container {
  background: initial;
  .head {
    color: #fff;
  }
  .tab {
    height: 60px;
    display: flex;
    align-items: center;
    position: relative;
    flex-wrap: nowrap;
    overflow-x: auto;
    padding: 0 15px;
    &::-webkit-scrollbar {
      display: none;
    }

    .tab-item {
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 15px;
      color: #000000;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.5);
      flex-shrink: 0;
      &:not(:last-child) {
        margin-right: 33px;
      }

      &.active {
        font-size: 17px;
        color: #fff;
      }
    }

    .tab-ink-bar {
      position: absolute;
      left: 0;
      bottom: 12px;
      width: 22px;
      height: 4px;
      background-color: transparent;
      transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s,
        left 0.3s cubic-bezier(0.35, 0, 0.25, 1);

      .tab-inner {
        border-radius: 0;
        display: block;
        margin: auto;
        height: 100%;
        background: linear-gradient(130deg, #447af4 25%, #33e7f7 100%);
        border-radius: 5px;
      }
    }
  }
  .card-floor-border {
    background-image: radial-gradient(at 0 0, #9b9a9a, #1e1e1e, #1d1d1c);
    margin: 0 0 20px;
    .border-in {
      background: linear-gradient(-20deg, #0a0a0a 0%, #131313 100%);
      border-radius: 15px;
    }
  }
  .redius-btn {
    width: 85px;
    height: 85px;
    background: linear-gradient(-50deg, #074aa3, #008dde);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: #ffffff;
  }
  .card-floor {
    padding: 20px 0 0;
    border-radius: 15px;
    .title {
      padding-bottom: 20px;
      margin: 0 15px;
      .left {
        font-size: 17px;
        color: #9b9a9a;
        font-weight: 600;
      }
      .right {
        font-size: 14px;
        color: #0d469f;
        font-weight: 600;
        padding: 10px 12px;
        border-radius: 8px;
        color: #9b9a9a;
        display: inline-block;
        line-height: 1;
        .arrow {
          width: 6.5px;
          height: 11px;
        }
      }
    }
    .overview {
      padding: 0 18px;
      .small {
        font-size: 16px;
      }
      .item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 32px 0;
        .key {
          font-size: 15px;
          font-weight: 400;
          color: #9b9a9a;
        }
        .value {
          font-size: 43px;
          font-weight: bold;
          color: #0093ed;
          margin: 13px 0 14px;
        }
      }
    }
    .overview-in {
      display: flex;
      align-items: center;
      padding: 41px 0;
      margin: 0 18px;
      flex-wrap: wrap;
      .small {
        font-size: 16px;
      }
      .item {
        width: 50%;
        &:nth-child(1n) {
          text-align: left;
        }
        &:nth-child(2n) {
          text-align: right;
        }
        .key {
          font-size: 15px;
          font-weight: 400;
          color: #9b9a9a;
        }
        .value {
          font-size: 29px;
          font-family: PingFang SC;
          font-weight: bold;
          color: #ffffff;
          padding-top: 11px;
          img {
            width: 28px;
            height: auto;
            display: block;
          }
        }
      }
    }
  }
  .form-wrap {
    padding: 18px 0 0;
    border-radius: 15px;
    .top {
      padding: 0 13px;
      margin: 0 4px;
    }
    .agreement {
      align-items: flex-start;
      padding: 0 6px;
      .key {
        font-size: 15px;
        color: #9b9a9a;
